
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
import MescrollVue from "mescroll.js/mescroll.vue";
export default {
components: {
Group,
XButton,
Countdown,
XHeader,
XInput,
MescrollVue
},
mounted() {

this.$nextTick(function() {
this.token = localStorage.getItem("token");
});
},
data() {
return {
canSave: true,
token: "",
code_val: "",
mobile_val: "",
countDownShow: false,
time: 60,
start: false,
valid_mobile: false,
mescrollDown: {
use: false
},
mescrollUp: {
use: false,
isBounce: false
}
};
},
computed: {},
methods: {
finish(index) {
this.start = false;
this.countDownShow = false;
},
countDown() {
if (this.valid_mobile) {
this.getCountTime();
} else {
this.$vux.toast.text("请输入正确手机号");
}
},
isWin() {
this.valid_mobile = this.$refs.valid_mobile.valid;
},
getCountTime(){
if(!this.canSave){
return;
}
this.canSave = false;

let params = {
token:  this.token,
api_name: 'time_down'
}

this.$ajax({
method: "post",
url: "/api/huser/api",
data: this.$qs.stringify(params)
})
.then(response => {
let data = response.data;
if (data.code == 1) {
this.time = +data.data.time_down;
this.regSendSMS();
} else {
this.$vux.toast.text(data.msg);
return;
}
})
.catch(error => {
this.$vux.toast.text("发送失败，请稍后重试");
});
},
regSendSMS() {

let params = {
token:  this.token,
api_name: 'send_sms',
tel: this.mobile_val
}

this.$ajax({
method: "post",
url: "/api/huser/api",
data: this.$qs.stringify(params)
})
.then(response => {
let data = response.data;
if (data.code == 1) {
this.$vux.toast.text("发送成功");
this.countDownShow = true;
this.start = true;
setTimeout(() => {
this.canSave = true;
}, 1000);
} else {
this.$vux.toast.text(data.msg);
setTimeout(() => {
this.canSave = true;
}, 1000);
return;
}
})
.catch(error => {
this.$vux.toast.text("发送失败，请稍后重试");
setTimeout(() => {
this.canSave = true;
}, 1000);
});
},
postData() {
if (this.$refs.valid_mobile.currentValue == "") {
this.$vux.toast.text("手机号未填写");
return;
} else if (this.$refs.code_input.currentValue == "") {
this.$vux.toast.text("验证码未填写");
return;
}
if (this.valid_mobile) {

let username = this.mobile_val;
let code = this.code_val;
let params = {
token:  this.token,
api_name: 'bind_tel',
tel: this.mobile_val,
code: this.code_val
}
this.$ajax({
method: "post",
url: "/api/huser/api",
data: this.$qs.stringify(params)
})
.then(response => {
let data = response.data;
if (data.code != 1) {
this.$vux.toast.text(data.msg);
return;
}
this.$vux.toast.text("绑定成功");
setTimeout(() => {
this.$router.back(-1);
}, 1000);
})
.catch(error => {
this.$vux.toast.text("系统错误，请稍后重试");
});
} else {
this.$vux.toast.text("请输入正确手机号");
return;
}
}
}
};



